<template>
    <!--   -->
<div class="mr-4" style="width: 20%">
            <v-tabs
            slider-color="blue" style="width: 100%; background-color: white;">
                <v-tab  style="width: 100%" :key=1 ripple>
                        <b></b>      
                        <i class='mdi mdi-close-circle-outline ml-2'></i>
                </v-tab>
                <v-tab  style="width: 100%" :key=2 ripple>
                        <b> </b>      
                        <i class='mdi mdi-close-circle-outline ml-2'></i>
                </v-tab>
               
               
                <v-tab-item>
                    <v-card flat>
                    <v-card-text>
                    <div class="col-12">
                                <div class="row mt-2">
                                        <div class="col-12 text-right">
                                            <i class=" mdi mdi-star-outline"></i>
                                        </div>
                                    </div>
                                <table-status-doc :status=statusDoc></table-status-doc>
                                <div class="row">
                                        <div class="col-12">
                                            <hr/>
                                            <div class="form-group">
                                                <label class="text-muted" for="exampleFormControlTextarea"></label>
                                                <textarea class="form-control" id="exampleFormControlTextarea" rows="5"></textarea>
                                            </div>
                                        </div>
                                    </div>
                                <div class="row collapse-list">
                                    <actions-document></actions-document>
                                </div>
                            </div>
                            </v-card-text>
                        </v-card>
                </v-tab-item>
            </v-tabs>
                   
</div>
</template>

<script>
  export default {
    name: 'task-bar',
    data () {
      return {
        status: this.statusDoc,
        fulltab: {
          width: '100%'
        }
      }
    },
    props: [
      'statusDoc'
    ]
  }
</script>

<style>
    .text-blue{
        color: #1e88e5;
    }
    .fulltab{
        width: 100%;
    }
    .mdi-star-outline{
        font-size: 1.5rem;
    }

</style>



