<template>
    <!--    ",    .."-->
    <div class="col col-3 " style="font-size: 1.2rem;">
        <div class="col-12">
            <v-tabs
            slider-color="blue" style="background-color: white;">
                <v-tab  :key=1 ripple>
                    <b></b>
                    <i class='mdi mdi-close-circle-outline ml-2'></i>
                </v-tab>
                <v-tab  :key=2 ripple>
                    <b> </b>
                    <i class='mdi mdi-close-circle-outline ml-2'></i>
                </v-tab>
                <v-tab  :key=3 ripple>
                    <b></b>
                    <i class='mdi mdi-close-circle-outline ml-2'></i>
                </v-tab>
                <v-tab  :key=4 ripple>
                    <b></b>
                    <i class='mdi mdi-close-circle-outline ml-2'></i>
                </v-tab>
                <v-tab  :key=5 ripple>
                    <b>...</b>
                    <i class='mdi mdi-close-circle-outline ml-2'></i>
                </v-tab>
                 <v-tab-item>
                    <v-card flat>
                        <v-card-text>
                            <div class="col-12">
                                <div class="row">
                                    <div class="col-12 bg-white text-right text-blue">
                                        <i class="mdi mdi-printer mt-3 fr"></i>
                                    </div>
                                </div>
                                <div class="row mt-2">
                                    <properti-document-tab :status-doc=statusDoc></properti-document-tab>
                                </div>
                            </div>
                        </v-card-text>
                    </v-card>
                 </v-tab-item>
                 <v-tab-item>
                        <v-card flat>
                            <v-card-text>
                                <div class="col-12">
                                    <div class="row">
                                        <document-log :doc-card-in=statusDoc></document-log>
                                    </div>
                                </div>
                            </v-card-text>
                        </v-card>
                 </v-tab-item>
                 <v-tab-item></v-tab-item>
                 <v-tab-item></v-tab-item>
                 <v-tab-item></v-tab-item>
            </v-tabs>
        </div>
    </div>
</template>

<script>
  import PropertiDocumentTab from '../doc_elements/Document-properti-tab'
  import DocumentLog from '../doc_elements/Document-log'
  export default {
    name: 'properti-document-bar',
    data () {
      return {
        compDoc: this.statusDoc
      }
    },
    props: [
      'statusDoc'
    ],
    components: {
      DocumentLog,
      PropertiDocumentTab
    }
  }
</script>

<style scoped>
    .show {
        display: block;
    }
    .text-blue{
        color: #1e88e5;
    }
    hr{
        border: none;
        background-color: #2a3035;
        color: #2a3035;
        height: 1px;
    }
    .collapse-list ul li{
        color: #455a64;
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn ul li{
        list-style-type: none;
        margin-left: -30px;
    }
    .toggle-btn-line ul li{
        display: inline;
        list-style-type: none;
    }
    i{
        font-size: 1.5rem;
    }

</style>



